/*КНОПКА НАВИГАЦИИ*/
const element = document.getElementById('navMenu');
const button = document.getElementById('navMenuBtn');

button.onclick = function () {
    if (element.classList.contains('visually-hidden')) {
        element.classList.remove('visually-hidden'); // Удаляем класс

    } else {
        element.classList.add('visually-hidden'); // Возвращаем класс

    }
}

